<template>
    <section id="experience">
        <div class="text row">
          <h1 v-if="!eng" class="col-md-3 text-left">DENEYIM</h1>
          <h1 v-if="eng" class="col-md-3 text-left">WORK</h1>
          <div class="col-md-9 center">
            <h2 v-if="!eng">İstanbul Teknik Üniversitesi</h2>
            <h2 v-if="eng">Enuygun.com</h2>
            <ul class="details">
              <li v-if="!eng">Bilgisayar Mühendisliği</li>
              <!-- <li v-if="eng">Internship /</li> -->
              <li> July 2018 - October 2018</li>
            </ul>
            <ul class="work_list">
                <li> Took part in a PHP-Python Computer Vision project and experienced OOP programming, Version Control Systems, Jira, in a professional environment </li>
                <li> Got a course about various web technologies for 2 weeks </li>
                <li> Implemented a Web Crawler project with Node.js </li>
            </ul>
          </div>
        </div>
    </section>
</template>

<script>
export default {
    data: () => {
        return {
            eng: true,
        }
    }
}
</script>

<style lang="scss">
#experience{
    padding: 70px 0% 3% 0;  
    
    font-size: 18px;
    line-height: 30px;

    .text{
      color: white;


      h1{
        opacity: 0.95;
      }

      h2{
        color: rgb(240, 240, 240);
      }

      ul.details{
        color: rgb(143, 143, 143);
        li{
          display: inline;
          // overflow: ;
          list-style-type: disc;
          // display: list-item;
        }
      }

      li{
        color: rgb(180, 176, 176);
        // list-style-type: none;
      }
    }
  }

  .work_list{
      list-style-type: square !important;
      padding-right: 20%;
      padding-left: 2%;
  }
</style>





